import {ViewEncapsulation, Injectable} from '@angular/core';
import {CompileIdentifierMetadata, CompileDirectiveMetadata} from './compile_metadata';
import * as o from './output/output_ast';
import {ShadowCss} from './shadow_css';
import {UrlResolver} from './url_resolver';
import {extractStyleUrls} from './style_url_resolver';
import {isPresent} from '../src/facade/lang';

const /** @type {?} */ COMPONENT_VARIABLE = '%COMP%';
const /** @type {?} */ HOST_ATTR = /*@ts2dart_const*/ `_nghost-${COMPONENT_VARIABLE}`;
const /** @type {?} */ CONTENT_ATTR = /*@ts2dart_const*/ `_ngcontent-${COMPONENT_VARIABLE}`;

export class StylesCompileDependency {
/**
 * @param {?} moduleUrl
 * @param {?} isShimmed
 * @param {?} valuePlaceholder
 */
constructor(public moduleUrl: string,
public isShimmed: boolean,
public valuePlaceholder: CompileIdentifierMetadata) {}

  static _tsickle_typeAnnotationsHelper() {
 /** @type {?} */
StylesCompileDependency.prototype.moduleUrl;
 /** @type {?} */
StylesCompileDependency.prototype.isShimmed;
 /** @type {?} */
StylesCompileDependency.prototype.valuePlaceholder;
  }

}

export class StylesCompileResult {
/**
 * @param {?} statements
 * @param {?} stylesVar
 * @param {?} dependencies
 */
constructor(public statements: o.Statement[],
public stylesVar: string,
public dependencies: StylesCompileDependency[]) {}

  static _tsickle_typeAnnotationsHelper() {
 /** @type {?} */
StylesCompileResult.prototype.statements;
 /** @type {?} */
StylesCompileResult.prototype.stylesVar;
 /** @type {?} */
StylesCompileResult.prototype.dependencies;
  }

}
export class StyleCompiler {
private _shadowCss: ShadowCss = new ShadowCss();
/**
 * @param {?} _urlResolver
 */
constructor(private _urlResolver: UrlResolver) {}
/**
 * @param {?} comp
 * @return {?}
 */
compileComponent(comp: CompileDirectiveMetadata): StylesCompileResult {
    var /** @type {?} */ shim = comp.template.encapsulation === ViewEncapsulation.Emulated;
    return this._compileStyles(getStylesVarName(comp), comp.template.styles,
                               comp.template.styleUrls, shim);
  }
/**
 * @param {?} stylesheetUrl
 * @param {?} cssText
 * @param {?} isShimmed
 * @return {?}
 */
compileStylesheet(stylesheetUrl: string, cssText: string,
                    isShimmed: boolean): StylesCompileResult {
    var /** @type {?} */ styleWithImports = extractStyleUrls(this._urlResolver, stylesheetUrl, cssText);
    return this._compileStyles(getStylesVarName(null), [styleWithImports.style],
                               styleWithImports.styleUrls, isShimmed);
  }
/**
 * @param {?} stylesVar
 * @param {?} plainStyles
 * @param {?} absUrls
 * @param {?} shim
 * @return {?}
 */
private _compileStyles(stylesVar: string, plainStyles: string[], absUrls: string[],
                         shim: boolean): StylesCompileResult {
    var /** @type {?} */ styleExpressions =
        plainStyles.map(plainStyle => o.literal(this._shimIfNeeded(plainStyle, shim)));
    var /** @type {?} */ dependencies = [];
    for (var /** @type {?} */ i = 0; i < absUrls.length; i++) {
      var /** @type {?} */ identifier = new CompileIdentifierMetadata({name: getStylesVarName(null)});
      dependencies.push(new StylesCompileDependency(absUrls[i], shim, identifier));
      styleExpressions.push(new o.ExternalExpr(identifier));
    }
    // styles variable contains plain strings and arrays of other styles arrays (recursive),
    // so we set its type to dynamic.
    var /** @type {?} */ stmt = o.variable(stylesVar)
                   .set(o.literalArr(styleExpressions,
                                     new o.ArrayType(o.DYNAMIC_TYPE, [o.TypeModifier.Const])))
                   .toDeclStmt(null, [o.StmtModifier.Final]);
    return new StylesCompileResult([stmt], stylesVar, dependencies);
  }
/**
 * @param {?} style
 * @param {?} shim
 * @return {?}
 */
private _shimIfNeeded(style: string, shim: boolean): string {
    return shim ? this._shadowCss.shimCssText(style, CONTENT_ATTR, HOST_ATTR) : style;
  }

  static _tsickle_typeAnnotationsHelper() {
 /** @type {?} */
StyleCompiler.prototype._shadowCss;
 /** @type {?} */
StyleCompiler.prototype._urlResolver;
  }

static decorators: DecoratorInvocation[] = [
{ type: Injectable },
];
static ctorParameters: {type: Function, decorators?: DecoratorInvocation[]}[] = [
{type: UrlResolver, },
];
}
/**
 * @param {?} component
 * @return {?}
 */
function getStylesVarName(component: CompileDirectiveMetadata): string {
  var /** @type {?} */ result = `styles`;
  if (isPresent(component)) {
    result += `_${component.type.name}`;
  }
  return result;
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
